import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// 引入组件
import home from '@/components/home/home.vue' 
import guide from '@/components/guide/guide.vue'

const routes = [
    {
        path: '', redirect: '/home'
    },
    {
        path: '/', redirect: '/home'
    },
    {
      path: '/home', name: 'home', component: home
    },
    // 指南界面
    {
        path: '/guide',
        name: 'guide',
        component: guide,
        children: [
            // markdown 会被渲染在 guide 的 <router-view> 中
            { path: '/guide/useIntroduct', component: ()=> import('@/assets/mark/guideUI.md') },
        ]
    },
    // 组件说明界面
    {
        path: '/introduce',
        name: 'introduce',
        component: ()=> import('@/components/guide/introduce.vue'),
        children: [
            { path: '/introduce',             component: ()=> import('@/assets/mark/component-markdown/log.md') },
            { path: '/introduce/log',         component: ()=> import('@/assets/mark/component-markdown/wzc-select.md') },
            { path: '/introduce/button',      component: ()=> import('@/assets/mark/component-markdown/wzc-button.md') },
            { path: '/introduce/select',      component: ()=> import('@/assets/mark/component-markdown/wzc-select.md') },
            { path: '/introduce/switch',      component: ()=> import('@/assets/mark/component-markdown/wzc-switch.md') },
            { path: '/introduce/slider',      component: ()=> import('@/assets/mark/component-markdown/wzc-slider.md') },
            { path: '/introduce/input',       component: ()=> import('@/assets/mark/component-markdown/wzc-input.md') },
            { path: '/introduce/picview',     component: ()=> import('@/assets/mark/component-markdown/wzc-picview.md') },
            { path: '/introduce/collapse',    component: ()=> import('@/assets/mark/component-markdown/wzc-collapse.md') },
            { path: '/introduce/dividingline',component: ()=> import('@/assets/mark/component-markdown/wzc-dividingline.md') },
            { path: '/introduce/timeline',    component: ()=> import('@/assets/mark/component-markdown/wzc-timeline.md') },
            { path: '/introduce/colorpicker', component: ()=> import('@/assets/mark/component-markdown/wzc-color-picker.md') }
        ]
    },
    // 组件展示界面
    {
        path: '/exampleDemo',
        name: 'exampleDemo',
        component: ()=> import('@/components/guide/exampleDemo.vue')
    }
]
  
const router = new VueRouter({
    mode: 'hash',
    routes: routes
})

export default router;